@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
body {
	margin: 0;
	padding-top: 50px;
	font-family: 'Montserrat', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

body {
	margin: 0;
	padding: 0;
}

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20px;
	box-shadow: 2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
		6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
		12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
		22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
		41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
		100px 100px 80px rgba(0, 0, 0, 0.07);
}

.eventData__content {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.eventData {
	padding: 40px;
	width: 100%;
}

.eventHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.forum {
	margin-top: 20px;
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.1);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.forum h3 {
	margin: 10px auto;
}

.messagesBox {
	display: flex;
	width: 100%;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 10px;
}

.messagesBox p {
	margin: 0;
	padding: 0;
}

.messagesBox__message {
	border-bottom: 1px solid #000;
	margin-bottom: 10px;
	width: 100%;
	display: flex;
	flex-direction: column;
	background-color: #fff;
	border-radius: 16px;
	padding: 10px;
}

.messagesBox__message--userScreenName {
	align-self: flex-end;
	color: rgba(0, 0, 0, 0.4);
}

.messagesBox__message--userDetails {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin-right: 20px;
}

.messagesBox__message img {
	max-width: 400px;
	margin-top: 20px;
	/* margin: auto; */
}

.messagesBox__message--messageText {
	flex: 1 1;
}

.inputField {
	margin: 20px;
	flex: 1 1;
	display: flex;
	justify-content: center;
	align-items: center;
}

.inputField input[type='text'],
.inputField input[type='file'],
.inputField button {
	margin-right: 10px;
	border: 0px solid #fff;
	border-radius: 10px;
	font-size: 1em;
	padding: 10px;
}

.forum__content {
}

.content-container {
    margin: 0 auto;
    padding: 0 1.6rem;
    max-width: 80rem;
  }
  .input-group {
    display: flex;
    flex-direction: row;
    margin-bottom: 1.6rem;
  }
  
  .input-group__item {
    margin-bottom: 1.2rem;
  }

  .buttonContainer {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.buttonContainer button {
	margin: 8px;
}
