  .form {
    width: 100%;
  }
  
  .form > h2 {
    text-align: center;
  }
  
  .form > form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  
  .form > form > p {
    cursor: pointer;
  }
  
  .error {
    background-color: tomato;
    color: white;
    border-radius: 6px;
    padding: 6px 12px;
    font-weight: bold;
    max-width: 400px;
    margin-bottom: 18px;
  }
  